.transactionHistory {
  background: #f5ffff;
  border-collapse: collapse;
  text-align: center;
  display: inline-table;
}
.transactionHistory th {
  color: #fff;
  background-color: #706d97;
}
.transactionHistory td {
  min-width: 150px;
  color: #696969;
  border: 1px solid #706d97;
}
.transactionHistory th,
td {
  padding: 10px;
  height: 20px;
}
.transactionHistory tr:nth-child(2n) {
  background-color: #706d97;
}
